.App {
  width: 100%;
  height: auto;
  min-width: 800px;
}
.App .app-warp .app-top-box {
  width: 100%;
  background: white;
}
.App .app-warp .app-warp-header {
  margin: 0 auto;
  padding: 0;
  max-width: 1024px;
}
.App .app-warp .app-warp-header .app-logo-box {
  width: 80px;
  height: 100%;
  float: left;
}
.App .app-warp .app-warp-header .app-logo-box img {
  margin-top: -10px;
  margin-left: 25px;
  width: 50%;
}
.App .app-warp .app-warp-content {
  width: 100%;
}
.App .app-warp .app-warp-content .app-content-area {
  margin: 0 auto;
  max-width: 1024px;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
